.ticker {
  margin-bottom: 87px;

  &__wrap {
    display: flex;
    overflow: hidden;
    gap: 20px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__list {
    display: flex;
    justify-content: space-around;
    flex-shrink: 0;
    gap: 20px;
  }

  &__item {
    padding: 13px 35px;
    border: 1px solid var(--havelock-blue-color);
    border-radius: 86px;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.2;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--light-color);
  }
}
