.messagesSent {
  padding-top: 145px;
  padding-bottom: 170px;

  &__content {
    row-gap: 32px;
  }

  &__number {
    grid-column: 4 span;
    justify-self: end;
    font-weight: 700;
    font-size: 60px;
    line-height: 1;
    color: var(--havelock-blue-color);
  }

  &__text {
    grid-column: 8 span;
    font-size: 60px;
    line-height: 1;
  }

  &__desc {
    grid-column: 12 span;
    font-size: 20px;
    text-align: center;
    opacity: 0.7;
  }
}
