.questions {
  &__container {
    padding-top: 90px;
    padding-bottom: 60px;
  }

  &__title {
    margin-bottom: 28px;
    font-size: 46px;
  }

  &__subtitle {
    display: inline-block;
    margin-bottom: 44px;
    font-size: 24px;
  }

  &__left {
    @include image-set("../img/questions-bg-img");

    grid-column: 6 span;
    margin-bottom: 56px;
    padding: 82px 50px;
    border-radius: 20px;
    background-position: right;
    background-size: contain;
    background-repeat: no-repeat;
    background-color: var(--surf-crest-color);

    &-caption {
      margin-bottom: 36px;
      font-size: 30px;
    }

    &-text {
      max-width: 313px;
      font-size: 18px;
    }
  }

  &__right {
    grid-column: 8/5 span;
  }

  &__address {
    font-style: normal;

    &-item {
      @include mb(44px);

      display: flex;
      column-gap: 34px;
    }

    &-svg {
      width: 52px;
      height: 52px;
    }

    &-wrap {
      display: flex;
      flex-direction: column;
      row-gap: 5px;
    }

    &-desc {
      font-weight: 500;
      font-size: 18px;
      line-height: 1.4;
      opacity: 0.4;
    }

    &-mail {
      font-weight: 500;
      font-size: 24px;
      line-height: 1.4;
      color: var(--text-color);
      transition: color 0.3s ease-in-out;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--havelock-blue-color);
      }

      &:active {
        color: var(--ocean-green-color);
      }
    }

    &-text {
      font-weight: 500;
      font-size: 24px;
    }
  }
}
