.ourAssistance {
  padding: 90px 0;

  &__container {
    position: relative;
  }

  &__title {
    margin-bottom: 28px;
    font-size: 46px;
  }

  &__desc {
    display: inline-block;
    margin-bottom: 78px;
    font-size: 24px;
  }

  &__btns {
    position: absolute;
    top: 0;
    right: 0;
    width: 112px;
    height: 80px;
  }

  &__slider-btn {
    @include flex-all-center;

    border-radius: 37px;
    border: 1px solid var(--east-bay-color);
    width: 40px;
    height: 40px;
    color: var(--seashell-color);
    background-color: var(--east-bay-color);

    &::after {
      font-size: 12px;
    }

    &--next::after {
      margin-left: 1px;
    }

    &--prev::after {
      margin-right: 2px;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      border: 1px solid var(--havelock-blue-color);
      background-color: var(--havelock-blue-color);
    }

    &:active {
      background-color: var(--east-bay-color);
    }

    &.focus-visible {
      outline: 1px solid var(--east-bay-color);
      outline-offset: 3px;
    }
  }

  &__wrapper {
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__sliders {
    padding-top: 30px;
    padding-left: 35px;
    padding-bottom: 90px;
  }

  &__card {
    position: relative;
    padding-top: 66px;
    padding-left: 58px;
    border-radius: 20px;
    max-width: 413px;
    height: 504px;

    &-surf {
      background-color: var(--surf-crest-color);
    }

    &-east {
      background-color: var(--east-bay-color);
    }

    &-havelock {
      background-color: var(--havelock-blue-color);
    }

    &-content {
      max-width: 313px;
    }

    &-title {
      margin-bottom: 31px;
      font-size: 24px;

      &_white {
        color: var(--seashell-color);
      }
    }

    &-quotes {
      width: 29px;
      height: 25px;
      margin-bottom: 17px;

      &_white {
        fill: var(--seashell-color);
      }
    }

    &-text {
      &_white {
        color: var(--seashell-color);
      }
    }

    &-smiley {
      position: absolute;
      right: -90px;
      bottom: -90px;
      width: 400px;
      height: 400px;

      &_dual {
        right: -30px;
      }

      &_high {
        bottom: -60px;
      }

      &_white {
        fill: var(--seashell-color);
      }
    }
  }
}
