.questions__accordion {
  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  .accordion__control {
    display: flex;
    align-items: center;
    column-gap: 27px;

    padding: 22px 26px;
    padding-right: 0;
    border-radius: 20px;
    background-color: var(--light-color);

    &:focus {
      outline: 2px solid var(--havelock-blue-color);
      outline-offset: 2px;
    }
  }

  .accordion__title {
    font-size: 24px;

    &-white {
      margin-bottom: 35px;
      color: var(--light-color);
    }
  }

  .accordion__content {
    padding: 0;

    &-wrap {
      padding: 22px 0 39px 88px;
      border-radius: 20px;
      background-color: var(--east-bay-color);
    }
  }

  .accordion__icon {
    @include image-set("../img/accordion-img");
    @include bg-cover;

    display: inline-block;
    width: 35px;
    height: 35px;
    border-radius: 100%;
    background-size: 17px 17px;
    background-color: var(--havelock-blue-color);
    transition: transform var(--accordion-time) ease-out,
      background-color var(--accordion-time) ease-out;
  }

  .accordion__text {
    max-width: 550px;
    color: rgba(255, 255, 255, 0.85);
  }
}

.is-open .accordion__icon {
  background-color: var(--ocean-two-color);
  transform: rotate(45deg);
}

.is-open .accordion__content {
  padding-top: 20px;
  opacity: 1;
}
