.session {
  padding: 81px 0;
  margin-bottom: 81px;

  &__title {
    font-size: 46px;
    text-align: center;
    margin-bottom: 81px;
  }

  &__list {
    position: relative;
  }

  &__item {
    grid-column: 4 span;

    &-line {
      position: absolute;
      top: 29%;
      left: 18%;
      z-index: -10;
      border: 1px dashed rgba(25, 27, 68, 0.23);
      width: 64%;
      height: 0px;
    }
  }

  &__img-block {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 27px;
    width: 120px;
    height: 120px;
  }

  &__tick {
    position: absolute;
    right: 34px;
    bottom: 24px;
    width: 21px;
    height: 21px;
    fill: var(--havelock-blue-color);
  }

  &__desc {
    display: flex;
    flex-direction: column;
    font-size: 24px;
    text-align: center;
  }
}
