.footer {
  margin: 10px;
  margin-bottom: 0;
  padding: 60px 0 70px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  backdrop-filter: blur(6px);
  background: linear-gradient(224deg, #a8dec1 0%, #8fc4f8 100%),
    rgba(255, 255, 255, 0.23);

  &__container {
    @include flex-all-sb;
  }

  &__left {
    display: flex;
    flex-direction: column;
    row-gap: 12px;
  }

  &__center {
    display: flex;
    column-gap: 86px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
  }

  &__link {
    font-weight: 500;
    line-height: 0;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--havelock-blue-color);
    }

    &:active {
      color: var(--ocean-green-color);
    }
  }

  &__right {
    &-list {
      display: flex;
      flex-direction: column;
      row-gap: 30px;
    }
  }

  &__copyright {
    font-size: 14px;
    line-height: 1.4;
    color: rgba(30, 30, 30, 0.7);
  }
}
