.tariffs {
  &__container {
    padding: 90px 0;
  }

  &__title {
    margin-bottom: 64px;
    font-size: 46px;
    text-align: center;
  }

  &__list {
    &-small {
      display: flex;
      flex-direction: column;
      row-gap: 30px;
      margin-bottom: 50px;
    }
  }

  &__item {
    grid-column: 4 span;
    padding: 49px 50px 46px;
    border-radius: 20px;
    background-color: var(--surf-crest-color);

    &-bay {
      background-color: var(--east-bay-color);
    }

    &-small {
      display: flex;
      align-items: center;
      column-gap: 17px;
    }
  }

  &__price {
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 20px;
    padding: 5px 10px;
    border-radius: 21px;
    font-weight: 600;
    font-size: 10px;
    line-height: 1.2;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--seashell-color);

    &-discount {
      color: var(--seashell-color);
      background-color: var(--havelock-blue-color);
    }
  }

  &__caption {
    margin-bottom: 36px;
    font-size: 30px;

    &-white {
      color: var(--light-color);
    }
  }

  &__dot {
    margin-bottom: 3px;
    border-radius: 100%;
    width: 9px;
    height: 9px;
    fill: var(--dodger-blu-color);

    &-white {
      fill: var(--light-color);
    }
  }

  &__text {
    font-size: 18px;
    opacity: 0.8;

    &-white {
      color: var(--light-color);
    }
  }

  &__link {
    margin-bottom: 14px;
  }

  &__desc {
    text-align: center;
    opacity: 0.7;

    &-white {
      color: var(--light-color);
    }
  }
}
