.firstSession {
  margin: 0 10px;
  margin-bottom: 90px;
  padding: 129px 0;
  border-radius: 20px;
  background-color: var(--surf-crest-color);

  &__left {
    grid-column: 7 span;
  }

  &__duration {
    display: inline-block;
    margin-bottom: 39px;
    padding: 16px 20px;
    border: 1px solid rgba(30, 30, 30, 0.59);
    border-radius: 90px;

    &-desc {
      font-weight: 600;
      font-size: 12px;
      line-height: 1.2;
      letter-spacing: 0.08em;
      text-transform: uppercase;
      color: var(--text-color);
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    margin-bottom: 58px;
    font-size: 46px;
    line-height: 1.1;
  }

  &__notice {
    display: flex;
    gap: 20px;

    &-percentage {
      margin-top: -9px;
      margin-bottom: 50px;
      font-weight: 500;
      font-size: 100px;
      color: var(--havelock-blue-color);
    }

    &-desc {
      display: flex;
      flex-direction: column;
      font-size: 24px;
    }
  }

  &__right {
    position: relative;
    grid-column: 5 span;

    &-image {
      position: absolute;
      top: -47px;
      left: 0;
    }
  }
}
