.service {
  padding: 0 10px 15px;
  &__list {
    gap: 20px;
  }

  &__item {
    border-radius: 20px;

    &-top {
      grid-column: 12 span;
      padding-top: 116px;
      padding-bottom: 132px;
      background: linear-gradient(221deg, #a8dec1 0%, #b7d7f0 100%),
        rgba(255, 255, 255, 0.4);
    }

    &-bottom {
      position: relative;
      display: flex;
      grid-column: 6 span;
      padding-top: 51px;
      padding-left: 40px;
      min-height: 486px;

      &--left {
        @include image-set("../img/service-bt-img-1");
        background-position: center bottom 35px;
        background-size: 350px 300px;
        background-repeat: no-repeat;

        background-color: var(--ocean-green-color);
      }

      &--right {
        @include image-set("../img/service-bt-img-2");
        background-position: center bottom 50px;
        background-size: 300px 300px;
        background-repeat: no-repeat;

        background-color: var(--havelock-blue-color);
      }
    }
  }

  &__image {
    display: block;
    margin: 0 auto;
    margin-bottom: 39px;
  }

  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 54px;
    line-height: 1.1;
  }

  &__tick {
    margin-right: 24px;
    width: 46px;
    height: 46px;
    fill: var(--light-color);
  }

  &__text {
    max-width: 535px;
    font-weight: 500;
    font-size: 24px;
    color: var(--light-color);
  }
}
